"use client";
import { TextField } from "@mui/material";

const CustomNumber = ({
    label,
    name,
    value,
    onChange,
    required,
    disabled,
    min,
    max,
    step,
}) => {
    const handleChange = (e) => {
        const val = e.target.value;
        // Allow empty or valid numeric input
        if (val === "" || /^[0-9]*\.?[0-9]*$/.test(val)) {
            onChange(e);
        }
    };

    return (
        <TextField
            size="small"
            variant="standard"
            label={label}
            name={name}
            type="number"
            value={value || ""}
            onChange={handleChange}
            fullWidth
            margin="dense"
            required={required}
            disabled={disabled}
            inputProps={{ min, max, step }}
            sx={{
                "& .MuiOutlinedInput-root": {
                    borderRadius: 2,
                    background: "#fff",
                    "& fieldset": {
                        borderColor: "#e5e7eb",
                    },
                    "&:hover fieldset": {
                        borderColor: "#7e5bef",
                    },
                    "&.Mui-focused fieldset": {
                        borderWidth: "2px",
                        borderColor: "#7e5bef",
                    },
                },
                "& .MuiInputLabel-root": {
                    transform: "translate(12px, 9px) scale(1)", // label slightly lower initially
                },
                "& .MuiInputLabel-shrink": {
                    transform: "translate(14px, -6px) scale(0.75)", // moves up only when focused/value exists
                },
                "& .MuiInputLabel-root.Mui-focused": {
                    color: "#7e5bef",
                },
            }}
        />
    );
};

export default CustomNumber;
