"use client";
import { useRouter, useParams } from "next/navigation";
import { useEffect, useState } from "react";
import {
  Typography,
  Box,
  Button,
  CircularProgress,
} from "@mui/material";
import { motion } from "framer-motion";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import SaveIcon from "@mui/icons-material/Save";
import CustomForm from "@/app/components/CustomForm";
import { getApi } from "@/utils/getApiMethod";
import { putApi } from "@/utils/putApiMethod";
import { Snackbar, Alert } from "@mui/material";

const EditUser = () => {
  const router = useRouter();
  const { id } = useParams();

  const [formSchema, setFormSchema] = useState([]);
  const [form, setForm] = useState({});
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);
  const [snackbar, setSnackbar] = useState({
    open: false,
    message: "",
    severity: "success", // success | error | warning | info
  });

  // === Fetch dynamic form fields ===
  useEffect(() => {
    const fetchUserFields = async () => {
      setLoading(true);
      try {
        const structureRes = await getApi("/fieldindex01/form?entity_name=Vehicle");
        const userDataRes = await getApi(`/vehicles/${id}`); // ðŸ‘ˆ API for user details

        if (structureRes?.structure) {
          const structure = structureRes.structure;
          setFormSchema(structure);

          // initialize with user data or empty defaults
          const initialForm = structure.reduce((acc, tab) => {
            tab.sections.forEach((section) => {
              section.fields.forEach((field) => {
                acc[field.key] =
                  userDataRes?.[field.key] ??
                  (field.type === "multiselect"
                    ? []
                    : field.type === "switch"
                      ? false
                      : "");
              });
            });
            return acc;
          }, {});
          setForm(initialForm);
        } else {
          console.error("Unexpected form response:", structureRes);
        }
      } catch (error) {
        console.error("Error fetching user form:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchUserFields();
  }, [id]);

  // === Handlers ===
  const handleChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };

  const handleSave = async () => {
    try {
      setSaving(true);
      console.log("Updating vehicle:", form);

      const res = await putApi(`/vehicles/${id}`, form);

      if (res?.status_code === 200) {
        setSnackbar({
          open: true,
          message: "Vehicle updated successfully âœ…",
          severity: "success",
        });

        // ðŸ”„ Re-fetch updated data
        const updated = await getApi(`/vehicles/${id}`);
        if (updated?.data) setForm(updated.data);
      } else {
        setSnackbar({
          open: true,
          message: "Failed to update vehicle",
          severity: "error",
        });
      }
    } catch (err) {
      console.error("Error updating vehicle:", err);
      setSnackbar({
        open: true,
        message: "Error updating vehicle",
        severity: "error",
      });
    } finally {
      setSaving(false);
    }
  };

  const handleBack = () => {
    router.back();
  };

  // === Loading UI ===
  if (loading) {
    return (
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          height: "80vh",
        }}
      >
        <CircularProgress />
      </Box>
    );
  }

  return (
    <>
      <motion.div
        initial={{ opacity: 0, scale: 0.9, x: -20 }}
        animate={{ opacity: 1, scale: 1, x: 0 }}
        exit={{ opacity: 0, scale: 0.9, x: 20 }}
        transition={{ duration: 1, ease: [0.4, 0, 0.2, 1] }}
        style={{
          height: "95vh",
          overflowY: "auto",
          display: "flex",
          flexDirection: "column",
        }}
      >
        {/* Header */}
        <Box
          sx={{
            flexShrink: 0,
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
          }}
        >
          <Box>
            <Typography variant="h6" sx={{ fontWeight: 700 }}>
              Edit User
            </Typography>
            <Typography variant="body2" sx={{ color: "#666" }}>
              Update the user details below.
            </Typography>
          </Box>

          <Box>
            <Button
              className="btn-primary"
              sx={{ textTransform: "none", marginRight: 1 }}
              startIcon={<SaveIcon />}
              onClick={handleSave}
              disabled={saving}
            >
              Update
            </Button>
            <Button
              className="btn-secondary"
              sx={{ textTransform: "none" }}
              startIcon={<ArrowBackIcon />}
              onClick={handleBack}
            >
              Back
            </Button>
          </Box>
        </Box>

        {/* Dynamic Form */}
        <CustomForm
          formSchema={formSchema}
          formData={form}
          onChange={handleChange}
        />
      </motion.div>
      <Snackbar
        open={snackbar.open}
        autoHideDuration={3000}
        onClose={() => setSnackbar({ ...snackbar, open: false })}
        anchorOrigin={{ vertical: "bottom", horizontal: "right" }}
      >
        <Alert
          onClose={() => setSnackbar({ ...snackbar, open: false })}
          severity={snackbar.severity}
          variant="filled"
          sx={{ width: "100%" }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </>
  );
};

export default EditUser;
