"use client";
import { TextField } from "@mui/material";

const CustomTextarea = ({
    label,
    name,
    value,
    onChange,
    required,
    disabled,
    placeholder,
    minRows = 1, // start as single line
    maxRows = 6, // allow up to 6 lines before scrolling
}) => (
    <TextField
        multiline
        minRows={minRows}
        maxRows={maxRows}
        fullWidth
        margin="dense"
        size="small"
        variant="standard"
        label={label}
        name={name}
        value={value || ""}
        onChange={onChange}
        required={required}
        disabled={disabled}
        placeholder={placeholder || "Enter details..."}
        InputLabelProps={{ shrink: true }}
        sx={{
            "& .MuiInputBase-root": {
                background: "#fcfcfc",
                borderRadius: 2,
                alignItems: "flex-start",
            },
            "& .MuiInputLabel-root": {
                color: "#666",
                fontWeight: 500,
                "&.Mui-focused": {
                    color: "#7e5bef",
                },
            },
            "& textarea": {
                lineHeight: 1.6,
                fontSize: "0.9rem",
                overflowY: "auto", // âœ… enables scroll once max height reached
                resize: "none", // prevents manual resize
                maxHeight: "150px", // âœ… sets maximum height before scroll
            },
        }}
    />
);

export default CustomTextarea;
