"use client";
import { TextField, MenuItem } from "@mui/material";

const CustomSelect = ({
  label,
  name,
  value,
  onChange,
  options = [],
  required,
  disabled,
}) => (
  <TextField
    select
    size="small"
    variant="standard"
    label={label}
    name={name}
    value={value || ""}
    onChange={onChange}
    fullWidth
    margin="dense"
    required={required}
    disabled={disabled}
    sx={{
      "& .MuiInputBase-root": {
        paddingBottom: "0px", // aligns underline with other fields
      },
      "& .MuiInput-input": {
        padding: "0px 0 1px", // reduces height
        fontSize: "0.875rem", // same font size as others
      },
      "& .MuiInputLabel-root": {
        fontSize: "0.875rem", // smaller consistent label size
      },
    }}
  >
    {options.map((opt, idx) => (
      <MenuItem key={idx} value={opt}>
        {opt}
      </MenuItem>
    ))}
  </TextField>
);

export default CustomSelect;
